<template>
  <div id="toolbar">
    <el-image
      class="imgProfile"
      :src="user.userProfile"
      :preview-src-list="[user.userProfile]"
      :alt="user.nickname"
    >
      <div slot="error" class="image-slot">
        <i class="el-icon-picture-outline"></i>
      </div>
    </el-image>
    <div id="btnBar">
      <div class="topBtnBar">
        <el-tooltip
          class="item"
          effect="dark"
          content=""
          placement="right"
        >
          <el-button
            @click="chooseChatList('')"
            class="toolBtn"
            size="small"
            ><i class="fa fa-comments fa-2x" aria-hidden="true"></i
          ></el-button>
        </el-tooltip>
        <el-tooltip
          class="item"
          effect="dark"
          content=""
          placement="right"
        >
          <el-button
            @click="chooseChatList('')"
            class="toolBtn"
            size="small"
            ><i class="fa fa-address-book-o fa-2x" aria-hidden="true"></i
          ></el-button>
        </el-tooltip>
        <el-tooltip
          class="item"
          effect="dark"
          content=""
          placement="right"
        >
          <el-button
            @click="chooseChatList('')"
            class="toolBtn"
            size="small"
            ><i class="fa fa-android fa-2x" aria-hidden="true"></i
          ></el-button>
        </el-tooltip>
        <el-tooltip
          class="item"
          effect="dark"
          content=""
          placement="right"
        >
          <el-button
            @click="chooseChatList('')"
            class="toolBtn"
            size="small"
            ><i class="fa fa-envelope fa-2x" aria-hidden="true"></i
          ></el-button>
        </el-tooltip>
      </div>
      <div class="bottomBtnBar">
        <el-tooltip
          class="item"
          effect="dark"
          content=""
          placement="right"
        >
          <el-button class="toolBtn" size="small" @click="showProfileDialog"
            ><i class="fa fa-user fa-2x" aria-hidden="true"></i
          ></el-button>
        </el-tooltip>
        <el-tooltip class="item" effect="dark" content="" placement="right">
          <el-popover
            placement="right"
            width="180"
            trigger="click"
            popper-class="moreListPopoverClass"
          >
            <ul id="moreList">
              <li @click="showFeedbackDialog"></li>
              <li></li>
            </ul>
            <el-button slot="reference" class="toolBtn" size="small"
              ><i class="fa fa-bars fa-2x" aria-hidden="true"></i
            ></el-button>
          </el-popover>
        </el-tooltip>
        <el-tooltip class="item" effect="dark" content="" placement="right">
          <el-button @click="exitSystem" class="toolBtn" size="small"
            ><i class="fa fa-sign-out fa-2x" aria-hidden="true"></i
          ></el-button>
        </el-tooltip>
        <el-dialog title="" :visible.sync="profileDialogVisible" show-close center>
            <!--  -->
            <span slot="title">
              
            </span>
      
            <!--  -->
            <div class="profile-content">
              <div class="profile-image">
                <img :src="this.user.userProfile" alt="">
              </div>
              <div class="profile-divider"></div>
              <div class="profile-details">
                <p><strong></strong>{{ this.user.id }}</p>
                <p><strong></strong>{{ this.user.username }}</p>
                <p><strong></strong>{{ this.user.nickname }}</p>
              </div>
            </div>
        </el-dialog>
      </div>
    </div>
    <el-dialog
      title=""
      :visible.sync="feedBackDialogVisible"
      class="feedbackDialog"
    >
      <textarea class="feedbackInput" v-model="feedBackContent"> </textarea>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleFeedbackSend"> </el-button>
        <el-button @click="feedBackDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "toolbar",
  data() {
    return {
      user: JSON.parse(window.sessionStorage.getItem("user")),
      feedBackDialogVisible: false,
      feedBackContent: "",
      profileDialogVisible: false // 
    };
  },
  methods: {
    //
    exitSystem() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.getRequest("/logout");
          sessionStorage.removeItem("user");
          //SessionStoragestate
          if (sessionStorage.getItem("state")) {
            sessionStorage.removeItem("state");
          }
          //
          this.$store.dispatch("disconnect");
          this.$router.replace("/");
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    //
    chooseChatList(listName) {
        console.log(listName);
      this.$store.commit("changeCurrentList", listName);
    },
    //
    showFeedbackDialog() {
      this.feedBackContent = "";
      this.feedBackDialogVisible = true;
    },
    //
    handleFeedbackSend() {
      let msgObj = {};
      msgObj.userId = this.user.id;
      msgObj.nickname = this.user.nickname;
      msgObj.username = this.user.username;
      msgObj.content = this.feedBackContent;
      console.log(msgObj);
      this.postRequest("/mail/feedback", msgObj).then((resp) => {
        if (resp) {
          this.feedBackDialogVisible = false;
        }
      });
    },
    //
    clearChatHistory() {
      this.$confirm(
        "(), ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      )
        .then(() => {
          //localStorage
          if (localStorage.getItem("chat-session")) {
            localStorage.removeItem("chat-session");
          }
          //Vuex
          this.$store.state.sessionStorage = {};
          //SessionStoragestate
          if (sessionStorage.getItem("state")) {
            sessionStorage.removeItem("state");
          }
          this.$message({
            type: "success",
            message: "",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    showProfileDialog() {
      this.profileDialogVisible = true;
    },
    // 
    closeProfileDialog() {
      this.profileDialogVisible = false;
    }
  },
};
</script>

