<template>
  <div class="chatTitle">
    <span class="titleName">{{
      this.$store.state.currentSession.nickname
        ? this.$store.state.currentSession.nickname
        : ""
    }}</span>
    <el-button class="moreBtn" size="small" icon="el-icon-more" @click="handleClick"></el-button>
    <el-dialog :visible="showAddPeopleDialog" @close="closeAddPeopleDialog">
        <h3></h3>
        <!--  -->
        <el-select v-model="selectedUser" placeholder="">
          <el-option
            v-for="item in this.$store.state.users"
            :key="item.id"
            :label="item.username"
            :value="item.id">
          </el-option>
        </el-select>
        <span slot="footer" class="dialog-footer">
          <el-button @click="showAddPeopleDialog = false"> </el-button>
          <el-button type="primary" @click="inviteToGroup"> </el-button>
        </span>
    </el-dialog>
    <el-dialog :visible="showUserInfoDialog" @close="closeUserInfoDialog">
        <h3></h3>
        <!--  -->
        <div class="profile-content" style="display: flex; justify-content: center; align-items: center;">
            <div class="profile-image">
              <img :src="this.currentSession.userProfile" alt="">
            </div>
            <div class="profile-divider"></div>
            <div class="profile-details">
              <p><strong></strong>{{ this.currentSession.username }}</p>
              <p><strong></strong>{{ this.currentSession.nickname }}</p>
            </div>
          </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "chattitle",
  data() {
    return {
      user: JSON.parse(window.sessionStorage.getItem("user")),
      showAddPeopleDialog: false, // 
      showUserInfoDialog: false, // 
      userInfo: null, // 
      selectedUser: null // 
    };
  },
  computed: mapState(["sessions","users", "currentSession", "errorImgUrl"]),
  methods: {
    inviteToGroup () {
      let currenGroupId = this.$store.state.groups.find(obj => obj.username == this.$store.state.currentSession.username).id;
      this.getRequest("/user/invite?groupid="+currenGroupId+"&userid="+this.selectedUser)
      .then(response => {
        if (response > 0) {
          console.log("success");
        } else {
          console.log("fail");
        }
        this.showAddPeopleDialog = false
      })
      .catch(error => {
        console.error(error);
        // this.$message({
        //   message: '',
        //   type: 'error'
        // });
        this.showAddPeopleDialog = false
      })
    },
    closeAddPeopleDialog () {
      this.showAddPeopleDialog = false
      this.selectedUser = null // 
    },
    handleClick() {
        const { currentSession } = this.$store.state;
      if (currentSession && !currentSession.username.includes('')) {
        this.openUserInfoDialog(currentSession);
      } else {
        this.openAddPeopleDialog();
      }
    },
    openAddPeopleDialog() {
      this.showAddPeopleDialog = true; // 
      // currentSession && currentSession.username != ''
    },
    closeAddPeopleDialog() {
      this.showAddPeopleDialog = false; // 
    },
    openUserInfoDialog(userInfo) {
      this.userInfo = userInfo; // 
      this.showUserInfoDialog = true; // 
    },
    closeUserInfoDialog() {
      this.showUserInfoDialog = false; // 
      this.userInfo = null; // 
    },
  }
};
</script>

<style scoped>
.chatTitle {
  height: 50px;
  width: 100%;
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  border-bottom: 0.5px solid #c7d2db;
}
.moreBtn {
  background-color: #eee;
  border: 0;
  height: 40px;
}
.titleName {
  margin: 10px 20px;
}
</style>
