<template>
  <div id="uesrtext">
    <div>
      <el-popover
        placement="top-start"
        width="400"
        trigger="click"
        class="emoBox"
      >
        <div class="emotionList">
          <a
            href="javascript:void(0);"
            @click="getEmo(index)"
            v-for="(item, index) in faceList"
            :key="index"
            class="emotionItem"
            >{{ item }}</a
          >
        </div>
        <el-button id="emojiBtn" class="emotionSelect" slot="reference">
          <i class="fa fa-smile-o" aria-hidden="true"></i>
        </el-button>
      </el-popover>
      <el-upload
        class="upload-btn"
        action="/file?module=group-chat"
        :before-upload="beforeAvatarUpload"
        :on-success="imgSuccess"
        :on-error="imgError"
        :show-file-list="false"
        accept=".jpg,.jpeg,.png,.JPG,JPEG,.PNG,.gif,.GIF"
      >
        <el-button id="uploadImgBtn" icon="el-icon-picture-outline"></el-button>
      </el-upload>
    </div>
    <textarea
      id="textarea"
      placeholder=" Ctrl + Enter "
      v-model="content"
      v-on:keyup="addMessage_usertext"
    >
    </textarea>
    <el-button
      id="sendBtn"
      type="primary"
      size="mini"
      @click="addMessageByClick"
      >(S)</el-button
    >
  </div>
</template>

<script>
import { mapState } from "vuex";
const appData = require("../../utils/emoji.json"); //emojijson

export default {
  name: "uesrtext",
  data() {
    return {
      faceList: [], //
      content: "",
    };
  },
  mounted() {
    for (let i in appData) {
      //json
      this.faceList.push(appData[i].char);
    }
  },
  computed: mapState(["sessions", "currentSession"]),
  methods: {
    addMessageByClick() {
      if (!this.content || this.content.match(/^[ ]*$/)) {
        this.$message({
          showClose: true,
          message: "",
        });
        return;
      }
      let msgObj = new Object();
      msgObj.content = this.content;
      msgObj.messageTypeId = 1;
      //
      if (this.currentSession.username.includes('')) {
        console.log(this.content);
        msgObj.groupName = this.$store.state.currentSession.username;
        this.$store.state.stomp.send(
          "/ws/groupChat",
          {},
          JSON.stringify(msgObj)
        );
      }
      //
      if (this.currentSession.username === "") {
        msgObj.fromNickname = this.$store.state.currentUser.nickname;
        msgObj.to = "";
        this.$store.state.stomp.send(
          "/ws/robotChat",
          {},
          JSON.stringify(msgObj)
        );
        //session
        this.$store.commit("addMessage", msgObj);
      }
      //
      else {
        msgObj.from = this.$store.state.currentUser.username;
        msgObj.fromNickname = this.$store.state.currentUser.nickname;
        msgObj.to = this.currentSession.username;
        let current = this.$store.state.currentUser;
        let currenTo = this.$store.state.users.find(obj => obj.username === msgObj.to);
        console.log(msgObj.to);
        console.log(this.$store.state.users);
        console.log(currenTo);
        msgObj.fromId = current.id;
        msgObj.toId = currenTo.id;
        console.log(msgObj);
        msgObj.notSelf = false;
        msgObj.invoke = "text send";
        this.$store.state.stomp.send("/ws/chat", {}, JSON.stringify(msgObj));
        //
        this.$store.commit("addMessage", msgObj);
      }
      //
      this.content = "";
    },
    addMessage_usertext(e) {
      if (e.ctrlKey && e.keyCode === 13 && this.content.length) {
        this.addMessageByClick();
      }
    },
    /**
     *       
     */
    //
    beforeAvatarUpload(file) {
      //
      if (this.currentSession.username == "") {
        this.$message.error("");
      }
      //
      let isLt1M = file.size / 1024 / 1024 < 1;
      console.log(file);
      if (!isLt1M) {
        this.$message.error(" 1MB!");
      }
      //
      let imgType = file.name.substring(file.name.lastIndexOf(".") + 1);
      imgType = imgType.toLowerCase();
      let isImg =
        imgType === "jpg" ||
        imgType === "png" ||
        imgType === "jpeg" ||
        imgType === "gif";
      if (!isImg) {
        this.$message.error("");
      }
      return isLt1M && isImg;
    },
    // 
    imgSuccess(response, file, fileList) {
      console.log("url" + response);
      let msgObj = new Object();
      msgObj.content = response;
      //
      msgObj.messageTypeId = 2;
      if (this.currentSession.username.includes('')) {
        this.$store.state.stomp.send(
          "/ws/groupChat",
          {},
          JSON.stringify(msgObj)
        );
      } else {
        msgObj.from = this.$store.state.currentUser.username;
        msgObj.fromNickname = this.$store.state.currentUser.nickname;
        msgObj.to = this.currentSession.username;
        msgObj.notSelf = false;
        msgObj.invoke = "graph";
        this.$store.state.stomp.send("/ws/chat", {}, JSON.stringify(msgObj));
        //
        this.$store.commit("addMessage", msgObj);
      }
    },
    // 
    imgError(err, file, fileList) {
      this.$message.error("");
    },
    //Emoji
    getEmo(index) {
      var textArea = document.getElementById("textarea");
      //
      function changeSelectedText(obj, str) {
        if (window.getSelection) {
          // IE
          textArea.setRangeText(str);
          // 
          textArea.selectionStart += str.length;
          textArea.focus();
        } else if (document.selection) {
          // IE
          obj.focus();
          var sel = document.selection.createRange();
          sel.text = str;
        }
      }
      changeSelectedText(textArea, this.faceList[index]);
      this.content = textArea.value; // data
      // console.log(this.faceList[index]);
      return;
    },
  },
};
</script>


