<template>
  <el-container>
    <el-header>
    </el-header>
    <el-main>
      <div class="loginContainer">
        <el-form
          ref="loginForm"
          :rules="rules"
          :model="loginForm"
          label-width="80px"
        >
          <h3 class="loginTitle">BUPTChat</h3>
          <el-form-item label=":" prop="username">
            <el-input
              type="text"
              v-model="loginForm.username"
              auto-complete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label=":" prop="password">
            <el-input
              type="password"
              v-model="loginForm.password"
              auto-complete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label=":" prop="code">
            <el-input
              type="text"
              @keydown.enter.native="submitLogin"
              v-model="loginForm.code"
              auto-complete="off"
              placeholder=""
              style="width: 150px"
            ></el-input>
            <img
              :src="verifyCode"
              title=""
              @click="changeverifyCode"
            />
          </el-form-item>
          <el-checkbox v-model="checked" class="loginRemember"></el-checkbox
          ><span> </span>
          <div>
            <el-button
              @click="showRegistryDialog"
              style="width: 45%; margin-right: 15px"
              ></el-button
            >
            <el-button
              type="primary"
              style="width: 45%"
              @click="submitLogin"
              v-loading.fullscreen.lock="fullscreenLoading"
              ></el-button
            >
          </div>
          <div style="height: 10px"></div>
          <div>
            <el-button
              type="primary"
              style="width: 97%"
              @click="openCamera"
              v-loading.fullscreen.lock="fullscreenLoading"
              ></el-button
            >
          </div>
        </el-form>
      </div>
    </el-main>

    <el-dialog
      title=""
      :before-close="closeCamera"
      :visible.sync="showCamera"
      width="100%"
    >
      <div class="box">
        <video
          id="videoCamera"
          class="canvas"
          :width="videoWidth"
          :height="videoHeight"
          autoPlay
        ></video>
        <canvas
          id="canvasCamera"
          class="canvas"
          :width="videoWidth"
          :height="videoHeight"
        ></canvas>
      </div>
      <div slot="footer">
        <el-button @click="drawImage" icon="el-icon-camera" size="small">
          
        </el-button>
        <el-button
          v-if="os"
          @click="getCompetence"
          icon="el-icon-video-camera"
          size="small"
        >
          
        </el-button>
        <el-button
          v-else
          @click="stopNavigator"
          icon="el-icon-switch-button"
          size="small"
        >
          
        </el-button>
        <el-button @click="resetCanvas" icon="el-icon-refresh" size="small">
          
        </el-button>
        <el-button
          @click="LoginWithFace"
          icon="el-icon-circle-close"
          size="small"
        >
          
        </el-button>
      </div>
    </el-dialog>

    <el-dialog
      title=""
      :before-close="closeRegisterDialog"
      :visible.sync="registerDialogVisible"
      width="30%"
    >
      <el-form
        :model="registerForm"
        status-icon
        :rules="registerRules"
        ref="registerForm"
      >
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="nickname"
        >
          <el-input
            v-model="registerForm.nickname"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="username"
        >
          <el-input
            v-model="registerForm.username"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="password"
        >
          <el-input
            type="password"
            v-model="registerForm.password"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="checkPass"
        >
          <el-input
            type="password"
            v-model="registerForm.checkPass"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-upload
            action="/file?module=group-chat"
            ref="upload"
            list-type="picture-card"
            :class="{ disabled: uploadDisabled }"
            :before-upload="beforeAvatarUpload"
            :file-list="fileList"
            :on-progress="onProgress"
            :on-success="imgSuccess"
            :on-error="imgError"
            :on-remove="imgRemove"
          >
            <i class="el-icon-plus"></i>
            <div slot="tip" class="el-upload__tip">
              4MB()
            </div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          @click="submitRegisterForm('registerForm')"
          style="width: 100%"
          ></el-button
        >
      </div>
    </el-dialog>

    <el-dialog
      title=""
      :before-close="closeSubmitface"
      :visible.sync="showSubmitface"
      width="100%"
    >
      <div class="box">
        <video
          id="videoCamera"
          class="canvas"
          :width="videoWidth"
          :height="videoHeight"
          autoPlay
        ></video>
        <canvas
          id="canvasCamera"
          class="canvas"
          :width="videoWidth"
          :height="videoHeight"
        ></canvas>
      </div>
      <div slot="footer">
        <el-button @click="drawImage" icon="el-icon-camera" size="small">
          
        </el-button>
        <el-button
          v-if="os"
          @click="getCompetence"
          icon="el-icon-video-camera"
          size="small"
        >
          
        </el-button>
        <el-button
          v-else
          @click="stopNavigator"
          icon="el-icon-switch-button"
          size="small"
        >
          
        </el-button>
        <el-button @click="resetCanvas" icon="el-icon-refresh" size="small">
          
        </el-button>
        <el-button @click="submitFace" icon="el-icon-circle-close" size="small">
          
        </el-button>
      </div>
    </el-dialog>
  </el-container>
</template>

<script>
export default {
  name: "Login",
  data() {
    var validateNickname = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      }
      //
      this.getRequest("user/checkNickname?nickname=" + value).then((resp) => {
        if (resp != 0) {
          callback(new Error(""));
        } else {
          callback();
        }
      });
    };
    var validateUsername = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      }
      //
      this.getRequest("/user/checkUsername?username=" + value).then((resp) => {
        if (resp != 0) {
          callback(new Error(""));
        } else {
          callback();
        }
      });
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.registerForm.checkPass !== "") {
          this.$refs.registerForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.registerForm.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        username: "",
        password: "",
        code: "",
      },
      verifyCode: "/verifyCode",
      checked: true,
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
        ],
        password: [{ required: true, message: "", trigger: "blur" }],
        code: [{ required: true, message: "", trigger: "blur" }],
      },
      fullscreenLoading: false,
      //
      registerDialogVisible: false,
      formLabelWidth: "120px",
      registerForm: {
        nickname: "",
        username: "",
        password: "",
        checkPass: "",
        userProfile: "default_head.jpg",
      },
      registerRules: {
        nickname: [{ validator: validateNickname, trigger: "blur" }],
        username: [{ validator: validateUsername, trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
      },
      uploadDisabled: false,
      //
      fileList: [],
      showCamera: false,
      showSubmitface: false,
      os: false, //
      thisVideo: null,
      thisContext: null,
      thisCancas: null,
      videoWidth: 500,
      videoHeight: 400,
      postOptions: [],
      CertCtl: "",
      // 
      loading: true,
    };
  },
  create() {
    this.getCompetence();
  },
  mount() {
    this.getCompetence();
  },
  methods: {
    submitLogin() {
      console.log("login!");
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.fullscreenLoading = true;
          this.postKeyValueRequest("/doLogin", this.loginForm).then((resp) => {
            setTimeout(() => {
              this.fullscreenLoading = false;
            }, 1000);
            if (resp) {
              //vuex
              this.$store.state.currentUser = resp.obj;
              //sessionStorage
              window.sessionStorage.setItem("user", JSON.stringify(resp.obj));
              let path = this.$route.query.redirect;
              this.$router.replace(
                path == "/" || path == undefined ? "/chatroom" : path
              );
            } else {
              this.changeverifyCode();
            }
          });
        } else {
          this.$message.error(",");
          return false;
        }
      });
    },
    changeverifyCode() {
      this.verifyCode = "/verifyCode?time=" + new Date();
    },
    gotoAdminLogin() {
      this.$router.replace("/adminlogin");
    },
    showRegistryDialog() {
      this.registerDialogVisible = true;
    },
    openCamera() {
      console.log("face!");
      this.showCamera = true;
      this.getCompetence();
    },
    closeCamera() {
      this.showCamera = false;
      this.stopNavigator();
    },
    LoginWithFace() {
      //
      this.fullscreenLoading = true;
      this.postKeyValueRequest("/doLoginWithFace", {
        personImg: this.imgSrc,
      }).then((resp) => {
        console.log(resp);
        console.log("postFace success");
        setTimeout(() => {
          this.fullscreenLoading = false;
        }, 1000);
        if (resp) {
          //vuex
          this.$store.state.currentUser = resp.obj;
          //sessionStorage
          window.sessionStorage.setItem("user", JSON.stringify(resp.obj));
          let path = this.$route.query.redirect;
          this.$router.replace(
            path == "/" || path == undefined ? "/chatroom" : path
          );
        } else {
          this.changeverifyCode();
        }
        this.closeCamera();
      });
    },
    // 
    getCompetence() {
      //modelrenderdom,modeldom
      this.$nextTick(() => {
        const _this = this;
        this.os = false; //
        this.thisCancas = document.getElementById("canvasCamera");
        this.thisContext = this.thisCancas.getContext("2d");
        this.thisVideo = document.getElementById("videoCamera");
        // mediaDevices
        if (navigator.mediaDevices === undefined) {
          navigator.menavigatordiaDevices = {};
        }
        // mediaDevices
        // getUserMedia
        // getUserMedia
        if (navigator.mediaDevices.getUserMedia === undefined) {
          navigator.mediaDevices.getUserMedia = function (constraints) {
            // getUserMedia()
            let getUserMedia =
              navigator.webkitGetUserMedia ||
              navigator.mozGetUserMedia ||
              navigator.getUserMedia;
            // 
            // 
            if (!getUserMedia) {
              return Promise.reject(
                new Error("getUserMedia is not implemented in this browser")
              );
            }
            // Promisenavigator.getUserMedia
            return new Promise(function (resolve, reject) {
              getUserMedia.call(navigator, constraints, resolve, reject);
            });
          };
        }
        const constraints = {
          audio: false,
          video: {
            width: _this.videoWidth,
            height: _this.videoHeight,
            transform: "scaleX(-1)",
          },
        };
        navigator.mediaDevices
          .getUserMedia(constraints)
          .then(function (stream) {
            // srcObject
            if ("srcObject" in _this.thisVideo) {
              _this.thisVideo.srcObject = stream;
            } else {
              // 
              _this.thisVideo.src = window.URL.createObjectURL(stream);
            }
            _this.thisVideo.onloadedmetadata = function (e) {
              _this.thisVideo.play();
            };
          })
          .catch((err) => {
            this.$notify({
              title: "",
              message: ".",
              type: "warning",
            });
          });
      });
    },
    //
    drawImage() {
      // canvas
      this.thisContext.drawImage(
        this.thisVideo,
        0,
        0,
        this.videoWidth,
        this.videoHeight
      );
      // base64
      this.imgSrc = this.thisCancas.toDataURL("image/png");
      /*const imgSrc=this.imgSrc;*/
    },
    //
    clearCanvas(id) {
      let c = document.getElementById(id);
      let cxt = c.getContext("2d");
      cxt.clearRect(0, 0, c.width, c.height);
    },
    //
    resetCanvas() {
      this.imgSrc = "";
      this.clearCanvas("canvasCamera");
    },
    //
    stopNavigator() {
      if (this.thisVideo && this.thisVideo !== null) {
        this.thisVideo.srcObject.getTracks()[0].stop();
        this.os = true; //
      }
    },
    /**/
    /**
     *       
     */
    //
    beforeAvatarUpload(file) {
      let isLt4M = file.size / 1024 / 1024 < 4;

      if (!isLt4M) {
        this.$message.error(" 4MB!");
      }
      return isLt4M;
    },
    // 
    onProgress(event, file, fileList) {
      this.uploadDisabled = true;
    },
    // 
    imgSuccess(response, file, fileList) {
      this.uploadDisabled = true;
      this.registerForm.userProfile = response; //
      console.log("url: " + this.registerForm.userProfile);
    },
    // 
    imgError(err, file, fileList) {
      this.$message.error("");
      this.uploadDisabled = false;
    },
    //
    imgRemove(file, fileList) {
      this.uploadDisabled = false;
    },
    closeRegisterDialog(done) {
      this.registerForm = {
        //
        nickname: "",
        username: "",
        password: "",
        checkPass: "",
        userProfile: "",
      };
      //this.$refs.upload.clearFiles();//
      done(); //
    },
    //
    submitRegisterForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.postRequest("/user/register", this.registerForm).then((resp) => {
            if (resp) {
              this.openSubmitface();
            }
          });
        } else {
          this.$message.error("");
          console.log("error submit!!");
          return false;
        }
      });
    },
    openSubmitface() {
      console.log("Submitface!");
      this.showSubmitface = true;
      this.getCompetence();
    },
    closeSubmitface() {
      this.showSubmitface = false;
      this.stopNavigator();
      this.registerDialogVisible = false;
      location.reload(); //
    },
    submitFace() {
      this.showSubmitface = false;
      this.postKeyValueRequest("/user/personImg/upload", {
        img: this.imgSrc,
        personName: this.registerForm.nickname,
        personId: this.registerForm.username,
      }).then((resp) => {
        if (resp) {
          console.log("success");
        }
        this.closeSubmitface();
      });
    },
  },
};
</script>


